<style scoped></style>

<template>
  <div class="memo">
    <div class="act">
      <button class="btn btn-primary" @click="add()">+ 추가</button>
    </div>
    <ul>
      <li v-for="d in data_1" :key="d.id" @click="edit(d.id)">
        {{ d.content }}
      </li>
    </ul>
  </div>
</template>

<script>
// vuex 라이브러리에서 mapActions, mapMutations, mapState, mapGetters 함를 가져옵니다.
// import { mapActions, mapMutations, mapState, mapGetters } from 'vuex';
import axios from "axios";
export default {
  /* pdtmc^2w */
  props: [],
  data() {
    /* 컴포넌트 안에서 사용되는 변수 등록. 개별 변수 */
    const data_1 = [];
    // 서버에서 어떤 데이터를 가져와서 보여줄 때
    axios.get("/api/memos").then((res) => {
      //성공했을때
      this.data_1 = res.data;
    });
    return { data_1 };
  },
  methods: {
    /* 이벤트 핸들러 등록 + 일반 함수 */
    add() {
      const content = prompt("내용을 입력해주세요");
      if (!content) {
        return alert("내용을 입력해주세요");
      }
      // 데이터 등록 및 전송
      // content : content 라 content 만 써도됨
      axios.post("/api/memos", { content }).then((res) => {
        this.data_1 = res.data;
      });
    },
    edit(id) {
      const content = prompt(
        "내용을 입력해 주세요",
        this.data_1.find((d) => d.id === id).content
      );
      // 데이터 수정
      axios.put("/api/memos/" + id, { content }).then((res) => {
        this.data_1 = res.data;
      });
    },
  },
};
</script>
<style scoped>
.memo .act {
  padding: 10px 10px 5px 5px;
  text-align: right;
}
.memo ul {
  list-style: none;
  padding: 15px;
  margin: 0;
}
.memo ul li {
  padding: 15px;
  margin: 10px 0;
  border: 1px solid #eee;
}
</style>
